.switch {
    margin-top: 30px;
    margin-right: 20px;
    position: relative;
    display: inline-block;
    width: 80px;
    height: 34px;
}
  /* Hide default HTML checkbox */
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}
  /* The slider */
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
}
.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 5px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
}
.slider-dark:before{
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 5px;
    bottom: 4px;
    background-color: black;
    transition: .4s;
}
input:checked + .slider {
    background: linear-gradient(to right, #4796d2 0%, #37ce8f 100%);
}
input:checked + .slider:before {
    transform: translateX(45px);
}
  /* Rounded sliders */
.slider.round {
    border-radius: 34px;
}
.slider.round:before {
    border-radius: 50%;
}
